<template>
  <div class="flex flex-col home-screen">
    <div class="flex-none">
      <header>
        <nav id="nav-back" class="flex mt-6 w-full lg:w-5/6 mx-auto justify-start md:justify-center">
          <div class="flex w-auto">
            <div class="title-page-container">
              <h1 ref="carouselTitle" class="text-color title-page-text text-2xl 2xl:text-3xl heading">{{ currentItemData.title }}</h1>
              <div ref="underline" class="bg-secondary-color title-page-underline h-0.5 bg-slate-900"></div>
            </div>
          </div>
        </nav>
      </header>
    </div>

    <div class="main home flex grow items-center w-full justify-center">
      <div class="container">

        <!-- GSAP Carousel per desktop -->
        <div class="hidden md:flex carousel justify-center items-center">
          <button ref="prevArrow" @click="prevItem" class="hidden border-color lg:block arrow prev border-t-2 border-l-2 h-4 w-4"></button>
          <p ref="carouselContent" class="text-color paragraph w-full lg:w-5/6 carousel text-center md:px-5 flex justify-center flex-wrap text-xl md:text-3xl xl:text-4xl 2xl:text-5xl">
            <span v-html="currentItemData.text"></span>
          </p>
          <button ref="nextArrow" @click="nextItem" class="hidden border-color lg:block arrow next border-t-2 border-l-2 h-4 w-4"></button>
        </div>

        <!-- Swiper Element Carousel per mobile -->
        <div ref="swiperContainer" class="opacity-0 block md:hidden carousel-swiper">
          <swiper-container 
            ref="swiper" 
            slides-per-view="1" 
            space-between="50">
            <swiper-slide v-for="(item, index) in items" :key="index">
              <div v-html="item.text" class="swiper-content text-color paragraph w-full text-start flex justify-center flex-wrap text-3xl">
              </div>
            </swiper-slide>
          </swiper-container>
        </div>

        <div class="block md:hidden mt-8">
          <p ref="swipe" class="swipe swipe-forward heading uppercase text-lg text-color inline opacity-100">swipe </p>
          <svg xmlns="http://www.w3.org/2000/svg" width="22" height="22" ref="swipeArrow" class="swipe-arrow inline fill opacity-0" viewBox="0 0 16 16">
            <path fill-rule="evenodd" d="M4 8a.5.5 0 0 1 .5-.5h5.793L8.146 5.354a.5.5 0 1 1 .708-.708l3 3a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708-.708L10.293 8.5H4.5A.5.5 0 0 1 4 8" />
          </svg>
        </div>

        <div ref="buttonContainer" class="flex justify-start md:justify-center mt-12 md:mt-12">
          <router-link to="/portfolio" class="text-color border-color button py-2 px-8 mr-4 paragraph uppercase text-base xl:text-lg 2xl:text-xl border-2 rounded-full" v-hover-animate>Portfolio</router-link>
          <router-link to="/resume" class="text-color border-color button py-2 px-8 mr-4 paragraph uppercase text-base xl:text-lg 2xl:text-xl border-2 rounded-full" v-hover-animate>Resume</router-link>
        </div>
      </div>
    </div>

    <div class="flex-none footer" ref="footerIcons">
      <SocialComponent svgClass="fill" />
    </div>
  </div>

  <MenuComponent />
</template>

<script>
import { gsap } from "gsap";
import { CSSPlugin } from "gsap/CSSPlugin";
import MenuComponent from "@/components/MenuComponent.vue";
import SocialComponent from "@/components/SocialComponent.vue";

gsap.registerPlugin(CSSPlugin);

export default {
  name: "Home",
  components: {
    MenuComponent,
    SocialComponent
  },
  data() {
    return {
      currentItem: 0,
      items: [
        {
          title: "Hello",
          text: "My name is Michele Paolino, <br>I'm a Napoli based web designer <br>and front-end developer.",
        },
        {
          title: "Skills",
          text: "Vue, WordPress, Bootstrap, Tailwind, <br>Figma, Adobe, UI design, Typography, <br> GSAP, Sass, Git, SEO.",
        },
        {
          title: "Swell Scales",
          text: `<p>I'm author of Swell Scales, <br>a design system for web devs. <br>Take a look on my <span><a class="underline underline-offset-8" href="https://github.com/michelediss?tab=repositories" target="_blank"><strong>GitHub.</strong></a></span></p>`,
        },
        {
          title: "Let's play!",
          text: `<p>Press <kbd class="c-key text-color">c</kbd> on your keyboard<br> or double tap on mobile to generate<br> a new website skin!</p>`,
        },
      ],
    };
  },
  computed: {
    currentItemData() {
      return this.items[this.currentItem];
    },
  },
  methods: {
    nextItem() {
      this.transitionSlide(() => {
        this.currentItem = (this.currentItem + 1) % this.items.length;
      });
    },
    prevItem() {
      this.transitionSlide(() => {
        this.currentItem = (this.currentItem - 1 + this.items.length) % this.items.length;
      });
    },
    goToItem(index) {
      if (index !== this.currentItem) {
        this.transitionSlide(() => {
          this.currentItem = index;
        });
      }
    },
    handleKeydown(event) {
      if (event.key === 'ArrowRight') {
        this.nextItem();
      } else if (event.key === 'ArrowLeft') {
        this.prevItem();
      }
    },
    transitionSlide(callback) {
      const carouselContent = this.$refs.carouselContent;
      const carouselTitle = this.$refs.carouselTitle;
      const underline = this.$refs.underline;

      if (carouselContent && carouselTitle) {
        gsap.to([carouselContent, carouselTitle], {
          duration: 0.5,
          opacity: 0,
          onComplete: () => {
            callback();
            gsap.to([carouselContent, carouselTitle], {
              duration: 0.5,
              opacity: 1,
            });
          },
        });
      }

      if (underline) {
        gsap.to(underline, {
          duration: 0.5,
          width: 0,
          opacity: 0,
          onComplete: () => {
            gsap.to(underline, {
              duration: 0.5,
              width: '100%',
              opacity: 1,
            });
          },
        });
      }
    },
    handleSlideChange() {
      const swiperInstance = this.$refs.swiper.swiper;
      this.transitionSlide(() => {
        this.currentItem = swiperInstance.activeIndex;
      });
    },
    setVH() {
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    }
  },
  mounted() {
    const tl = gsap.timeline();

    const carouselTitle = this.$refs.carouselTitle;
    const underline = this.$refs.underline;
    const carouselContent = this.$refs.carouselContent;
    const prevArrow = this.$refs.prevArrow;
    const nextArrow = this.$refs.nextArrow;
    const buttonContainer = this.$refs.buttonContainer;
    const footerIcons = this.$refs.footerIcons;
    const swipe = this.$refs.swipe;

    if (carouselTitle && underline && carouselContent && prevArrow && nextArrow && buttonContainer && footerIcons) {
      tl.to(carouselTitle, { opacity: 1, y: 0, duration: 0.5 }, "+=0.5");
      tl.to(underline, { opacity: 1, width: '100%', duration: 0.5 }, "-=0.3");
      tl.to(carouselContent, { opacity: 1, y: 0, duration: 0.5 }, "-=0.3");
      tl.to(this.$refs.swiperContainer, { opacity: 1, y: 0, duration: 0.5 }, "-=0.3");
      tl.to(this.$refs.swipe, { opacity: 1, y: 0, duration: 0.5 }, "-=0.3");
      tl.to(this.$refs.swipeArrow, { opacity: 1, y: 0, duration: 0.5 }, "-=0.3");

      const buttons = buttonContainer.querySelectorAll('.button');
      if (buttons.length > 0) {
        tl.to([...buttons], { opacity: 1, y: 0, stagger: 0.2, duration: 0.5 }, "-=0.3");
      }

      gsap.set([prevArrow, nextArrow], { display: 'block' });
      tl.to([prevArrow, nextArrow], { opacity: 1, duration: 0.5 }, "-=0.3");

      tl.to(footerIcons, { opacity: 1, y: 0, duration: 0.5 }, "-=0.3");
    }

    // Aggiungi l'evento di cambio slide
    this.$refs.swiper.swiper.on('slideChange', this.handleSlideChange);

    window.addEventListener('keydown', this.handleKeydown);
    
    // Imposta la variabile vh
    this.setVH();
    window.addEventListener('resize', this.setVH);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.setVH);
  }
};
</script>

<style scoped>
.home-screen {
  min-height: calc(var(--vh, 1vh) * 100);
}

.title-page-underline,
p,
h1,
.button,
.footer,
.arrow {
  opacity: 0;
}

.title-page-underline {
  width: 0;
}

span,
a,
strong {
  display: inline;
}

.swipe-arrow {
  display: inline-block;
  animation: bounceX 2s infinite;
}

@keyframes bounceX {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px);
  }
}
</style>
